.boton-contenedor{
    height: 80px;
    display:flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border-radius: 20px;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
}
.boton-contenedor:hover{
    background-color: #5a01a7;
}

.operador{
    background-color: green;
}

.operador:hover{
    background-color: orange;
}