.App{
  width: 1000%;
  height: 100vh;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display: flex;
  flex-wrap: wrap;

}
.container{
  width: 100%;
  display: flex;
  justify-content: center;
